
.quotesPage__mainDiv{
    display: flex;
    justify-content: center;
    width: 100vw;
    min-width: 500px;
     overflow: auto;
}

.quotesPage__mainDiv-2{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    min-width: 500px;
    width: 42vw;
}


.sortButton{
    background-color: rgb(213, 227, 236);
    color: rgb(95, 146, 212);;
    border: 1px solid rgb(95, 146, 212);
     border-radius: 8px;
    margin-top: 4vh;
    width: 160px;
    height: 40px;
    
    font-weight: 500;
}

.sortButton:hover{
    border-width: 3px;
    cursor: pointer;
}

.quoteListDiv{
    display: flex;
    flex-direction: column;
}

.quoteDiv{
    min-height: 100px;
    width: 100%;
    background-color: rgb(166, 219, 219);
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 3vh;
    border-radius: 8px;
}

.quoteDiv2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 1rem;
}

.viewFullScreenButton{
    background-color: rgb(42, 68, 75);
    color: white;
    margin-right: 16px;
    width: 160px;
    height: 40px;
    border-radius: 8px;
    transition: transform 0.05s ease;
    flex-shrink: 0;
}

.viewFullScreenButton:hover{
    cursor: pointer;
    transform: scale(1.04);
}

.quoteText{
    width: 90%;
    color:black;
    font-weight: 500;
    font-size: 24px;
    margin-bottom: 2px;
    margin-top: 8px;
    margin-left: 16px;
    word-break: break-word;
    line-height: 1.5;
}
.quoteAuthor{
    color:rgb(51, 54, 47);
    font-weight: 400;
    font-style: italic;
    font-size: 16px;
    margin-bottom: 8px;
    margin-top: 0;
    margin-left: 16px;
}

.lineDiv{
    width: 42vw;
    margin-top: 1vh;
    border-top: 4px solid rgba(0, 0, 0, 0.205);
}