.helperFullQuoteDiv{
    width: 100vw;
    min-height: 250px;
    display:flex;
    flex-direction: column;
    align-items: center;
}

.mainFullQuoteDiv{
    width:42vw;
    height: 30vh;
    min-height: 250px;
    min-width: 400px;
    margin-top: 4vh;
    border-radius: 8px;
    background-color: rgb(30, 14, 51);
    color:rgb(241, 248, 245);
    display: flex;
    align-items: center;
}


.helperDiv{
    height: 22vh;
    min-height: 200px;
    min-width: 370px;
    width: 37vw;
    margin-left: 3vw;
    margin-right: 2vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.quotePharagraph{
    font-size: 32px;
    font-weight: 500;
    min-height: 80px;
    align-self: flex-start; 
    scroll-behavior: smooth;
    word-break: break-word;
    overflow: auto;
    
}

.quotePharagraph::-webkit-scrollbar {
    width: 10px;
  }
  
  .quotePharagraph::-webkit-scrollbar-track {
    background-color: rgb(241, 248, 245);
  }
  
  .quotePharagraph::-webkit-scrollbar-thumb {
    background-color: rgb(138, 142, 143);
  }
  
  
  
  




.authorPharagraph{
    font-size: 20px;
    font-weight: 400;
    font-style: italic;
    align-self: flex-end; /* align to the right bottom */
  margin-top: auto;
}

.loadCommentsButton{
    width: 160px;
    height: 40px;
    margin-top: 4vh;
    color: rgba(0, 0, 0, 0.63);
    border: none;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 500;
    background-color: transparent;
}
.loadCommentsButton:hover{
    cursor: pointer;
    border: black solid 1px;
    background-color: rgb(88, 130, 136);
    color: white;

}


