

.centered{
   
        display: flex;
        flex-direction: column;
        width: 100vw;
        font-size: 48px;
        font-weight: 500;
        align-items: center;
        min-width: 500px;
       
}



.addQuoteButton{
        background-color: rgb(57, 87, 54);
        color: white;
        width: 160px;
        height: 40px;
        border-radius: 8px;
        transition: transform 0.05s ease;
        flex-shrink: 0;
    }
    
    .addQuoteButton:hover{
        cursor: pointer;
        transform: scale(1.04);
    }